import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { DataService } from './data.service';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';
import axios from 'axios';
@Injectable({
  providedIn: 'root',
})
export class OrderService {
  headers = new HttpHeaders({
    'Content-Type': 'application/json; charset=utf-8',
  });
  storeId: any;
  constructor(private http: HttpClient, private data: DataService) {}

  createOrder(body: any) {
    return this.http.post(`${environment.BASE_URL}/order/create`, body);
  }

  getOrder(id: any) {
    return this.http.get(`${environment.BASE_URL}/order/${id}`);
  }

  // updateOrder(id:any,body:any){
  //   return this.http.put(`${environment.BASE_URL}/order/update/${id}`,body);
  // }

  // deleteOrder(id:any){
  //   return this.http.delete(`${environment.BASE_URL}/order/delete/${id}`);
  // }

  getOrderByStoreId(): Observable<any> {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    this.storeId = this.storeId.user.storeId;

    return this.http.get(
      `${environment.BASE_URL}/order/store/${this.storeId}`,
      { headers: headers }
    );
  }
  getOrderBySid() {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    const sId = this.storeId.user.storeId;
    return axios.get('https://valart-api.herokuapp.com/order/store/' + sId, {
      headers: {
        Authorization: token,
      },
    });
  }
  paymentDetailsById(id: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    const storeId = JSON.parse(currentUser);
    var token = storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    const url = environment.BASE_URL + '/order/' + id;
    return this.http.get(url, { headers: headers });
  }

  createShiprocketOrder(orderId: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    const body = {
      orderId: orderId,
    };
    return this.http.post(
      `${environment.BASE_URL}/user/createShipRocketOrder`,
      body,
      { headers: headers }
    );
  }

  generateManifest(shipment_id: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });

    const body = {
      shipment_id: shipment_id,
    };
    return this.http.post(
      `${environment.BASE_URL}/user/generateManifest`,
      body,
      { headers: headers }
    );
  }

  generateLabels(shipment_id: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    const body = {
      shipment_id: [shipment_id],
    };

    return this.http.post(`${environment.BASE_URL}/user/generateLabel`, body, {
      headers: headers,
    });
  }

  generateInvoice(id: any = []) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    const body = {
      ids: [id],
    };

    return this.http.post(
      `${environment.BASE_URL}/user/generateInvoice`,
      body,
      { headers: headers }
    );
  }

  downloadInvoice(orderId: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    return this.http.get(
      `${environment.BASE_URL}/user/fetchOrderInvoice/${orderId}`,
      { headers: headers }
    );
  }
  fetchShiprocketOrder(orderid: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    return this.http.get(
      `${environment.BASE_URL}/user/fetchShipRocketOrder/${orderid}`,
      { headers: headers }
    );
  }

  cancelOrderProduct(orderid: any, productid: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    const body = {
      productId: productid,
      reasonforCancel: 'i dont want this product now',
    };

    return this.http.post(
      'https:valart-api.herokuapp.com/user/cancelOrderBeforeDispatch_admin/' +
        orderid,
      body,
      { headers: headers }
    );
  }

  cancelOrderFood(orderid: any, foodid: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    const body = {
      foodId: foodid,
      reasonforCancel: 'i dont want this product now',
    };

    return this.http.post(
      'https:valart-api.herokuapp.com/user/cancelOrderBeforeDispatch_admin/' +
        orderid,
      body,
      { headers: headers }
    );
  }

  cancelOrderAfterDispatchProduct(orderid: any, productid: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    const body = {
      productId: productid,
      reasonforCancel: 'i dont want this product now',
    };

    return this.http.post(
      'https:valart-api.herokuapp.com/user/cancelOrderAfterDispatch_admin/' +
        orderid,
      body,
      { headers: headers }
    );
  }

  cancelOrderAfterDispatchFood(orderid: any, foodid: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    const body = {
      foodId: foodid,
      reasonforCancel: 'i dont want this product now',
    };

    return this.http.post(
      'https:valart-api.herokuapp.com/user/cancelOrderAfterDispatch_admin/' +
        orderid,
      body,
      { headers: headers }
    );
  }

  InitiateshippingProduct(orderid: any, productId: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    const body = {
      orderId: orderid,
      productId: productId,
    };

    return this.http.post(
      'https:valart-api.herokuapp.com/user/generateAwbRequestShipmentPickup',
      body,
      { headers: headers }
    );
  }

  InitiateshippingFood(orderid: any, foodId: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ Authorization: token });
    const body = {
      orderId: orderid,
      productId: foodId,
    };

    return this.http.post(
      'https:valart-api.herokuapp.com/user/generateAwbRequestShipmentPickup',
      body,
      { headers: headers }
    );
  }
}
